* {
  box-sizing: border-box;
  padding: 0;
  margin: 0;
}

html {
  font-size: 62.5%;
}

body {
  margin: 0;
  font-family: 'Kumbh Sans', sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

img {
  max-width: 100%;
  object-fit: cover;
}

h1 {
  font-size: clamp(3rem, 6vw, 5rem);
}

.page-title {
  color: hsl(25, 100%, 50%);
}

.page-title, .page-content {
  text-align: center;
}

.Men, .Women, .About, .Contact {
  display: flex;
  flex-direction: column;
  gap: 30px;
  min-height: 100vh;
}

.route-page-container {
  width: 100%;
  min-height: 100vh;
  padding: 6rem 0rem;
}

@media screen and (min-width: 1440px) {
  .route-page-container {
    padding: 6rem 30rem;
  }
}

h2 {
  font-size: clamp(2.5rem, 3vw, 3.5rem);
}

p, a {
  font-size: clamp(1.6rem, 2vw, 2rem);
}

a {
  text-decoration: none;
  color: black;
  padding: 1em;
}

ul {
  list-style: none;
}

.container {
  display: flex;
  flex-direction: column;
}

@media screen and (min-width: 1440px) {
  .container {
    flex-direction: row;
    gap: 200px;
    margin-top: 50px;
  }
  .container__column1, .container__column2 {
    flex-basis: 50%;
  }
  .container__column2 {
    display: flex;
    flex-direction: column;
    gap: 60px;
  }
}